import React from 'react';

const Banner = ({ appName, token }) => {
  if (token) {
    return null;
  }
  return (
    <div className="banner">
      <div className="container">
        <h1 className="logo-font">
        </h1>
        <p>Share your fitness achievements!</p>
      </div>
    </div>
  );
};

export default Banner;



